@import "../../app.scss";

.product{
    // width: 90vw;
    // width: calc(300rem/16);
    // width: 20rem;
    width: 30%;
    height: 40vh;
    margin: 1.2rem 0.6rem;
    border: 0.15rem solid rgb(243, 243, 242);
    border-radius: 0.6rem 0.6rem 0 0;
    overflow: hidden;

    position: relative;

    @include media(mobile){
        width: 100%;
        height: 32vh;
        margin: 0;
        margin-bottom: 0.6rem;
    }

    .p-browser{
        height: 1.2rem;
        background-color: rgb(243, 243, 242);
        display: flex;
        align-items: center;
        justify-content: flex-end;
        position: sticky;
        z-index: 2;

        .p-circle{
            width: 0.6rem;
            height: 0.6rem;
            border-radius: 50%;
            background-color: pink;
            margin: 0 0.1rem;

            &:nth-of-type(1){
                background-color: lime;
            }
            &:nth-of-type(2){
                background-color: yellow;
            }
            &:nth-of-type(3){
                background-color: pink;
            }

        }
    }

    a{
        .p-img{
            width: 100% ;
            transition: all 1s ease;
            height: 100%;
            object-fit: cover;
            @include media(mobile){
                object-fit: cover;
                // display: none;
            }
            // display: none;


            &:hover{
                transform: translateY(-25%);
            }
        }
    }

    .more{
        position: absolute;
        top: 1.2rem;
        text-align: center;
        width: 100%;
        cursor: pointer;
        color: white;
        z-index: 2;
        // background-color: white;
        // margin-left: 50%;
    }

    .hidden-more{
        position: absolute;
        bottom: 0;
        text-align: center;
        width: 100%;
        cursor: pointer;
        color: red;
        z-index: -1;
        // margin-left: 50%;
    }
}

