@import "../../app.scss";

.contact{
    height: max-content;
    position: relative;
    // background-color: pink;

    @include media(mobile){
        // overflow: hidden;
        // background-color: yellow;
        height: max-content;
    }

    .c-bg{
        width: 1.2rem;
        height: 100%;
        background-color: #59b256;
        position: absolute;

        @include media(mobile){
            height: 45%;
            width: 1rem;
        }
    }

    .c-wrapper{
        display: flex;
        padding: 3rem;

        @include media(mobile){
            flex-direction: column;
            padding: 0 2rem;
        } 

        .c-left{
            flex: 1;

            .c-title{
                width: 80%;
                // background-color: pink;
                font-size: 4rem;
                @include media(mobile){
                    font-size: 2.5rem;
                    width: 100%;
                }
            }

            .c-info{

                .c-info-item{
                    display: flex;
                    margin: 3rem 0;
                    font-weight: 300;
                    width: 70%;
                    align-items: center;
                    @include media(mobile){
                        margin: 1.4rem 0;
                        width: 90%;
                    }

                    .c-icon{
                        width: 2rem;
                        height: 2rem;
                        margin-right: 1.2rem;
                    }
                }
            }
        }

        .c-right{
            flex: 1;
            display: flex;
            flex-direction: column;
            // align-items: center;
            justify-content: center;

            .c-description{
                font-weight: 200;
            }

            form {
                margin-top: 1.2rem;

                display: flex;
                flex-direction: column;
                gap: 0.8rem;

                input{
                    height: 3rem;
                    width: 100%;
                    border: none;
                    border-bottom: 0.1rem solid black;
                    // background-color: pink;
                    font-size: 1rem;
                    padding-left: 0.3rem;
                    @include media(mobile){
                        width: 100%;
                    }

                    &:nth-of-type(1){
                        width: 70%;
                    }
                    &:nth-of-type(2){
                        width: 80%;
                    }
                    &:nth-of-type(3){
                        width: 90%;
                    }
                }


                textarea{
                    width:  100%;
                    margin-top: 1rem;
                }

                .submit-button{
                    border: none;
                    padding: 1rem;
                    background-color: #59b256;
                    color: white;
                    font-size: 1.2rem;
                    font-weight: bold;
                    border-radius: 0.4rem;
                    cursor: pointer;

                    @include media(mobile){
                        margin-bottom: 1rem;
                    }
                }
            }
        }
    } 
}