@import "../../app.scss";

.pl{
    padding: 3rem 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 100%;
    // height: auto;

    @include media(mobile){
        padding: 1rem 1.1rem;
        // background-color: bisque;
        
    }

    .pl-texts{
        width: 70%;

        @include media(mobile){
            width: 100%;
        }

        .pl-title{
            font-size: 2.5rem;
            font-weight: 600;
            animation: blinking 3s ease infinite alternate;
        }
        @keyframes blinking {
            from{
                opacity: 0.3;
            }
            to{
                opacity: 1;
            }
        }

        .pl-description{
            margin: 1.3rem 0;
            @include media(mobile){
                // margin: 1.3rem 0 0 0.8rem;
            }
        }
    }

    .pl-list{
        width: 100%;        
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        @include media(mobile){
            // flex-direction: column;
        }
    }
}