@import "../../app.scss";

.i{
    display: flex;
    height: 100vh;
    // background-color: aqua;

    @include media(mobile){
        // background-color: yellow;
        flex-direction: column;
    }

    .i-left{
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        // background-color: aqua;

        @include media(mobile){
            flex-direction: column;
            justify-content: flex-start;
            text-align: center;
            padding-top: 2rem;
        }
    

        .i-left-wrapper{
            padding: 3rem;
            // background-color: pink;
            height: 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            @include media(mobile){
                padding: 0.6rem;
                height: 100%;
                align-items: center;
                text-align: center;
                justify-content: center;
                
                // overflow: hidden;
                
            }

            .i-intro{
                font-size: 2rem;
                font-weight: 300;
                @include media(mobile){
                    text-align: center;
                }
            }

            .i-name{
                font-size: 4rem;
                @include media(mobile){
                    font-size: 2.5rem;
                    margin: 1rem 0 2rem 0;
                }
            }

            .i-title{
                height: 3rem;
                color: lime;
                // background-color: lime;
                overflow: hidden;
                // note overflow hides a lot
                @include media(mobile){
                    width: 100%;
                    // background-color: red;
                }

                .i-title-wrapper{
                    // background-color: pink;
                    height: 100%;
                    animation: move 10s ease-in-out infinite ;
                    //alternate has been removed from the animation property
                    @include media(mobile){
                        text-align: center;
                        // // display: flex;
                        // align-items: center;
                        // justify-content: center;
                    }


                    @keyframes move {
                        25%{
                            transform: translateY(-3rem);
                        }
                        25%{
                            transform: translateY(-6rem);
                        }
                        25%{
                            transform: translateY(-9rem);
                        }
                        25%{
                            transform: translateY(-12rem);
                        }
                    }
                    
                    .i-title-item{
                        height: 3rem;
                        font-weight: bold;
                        font-size: 2rem;
                        color: #59b256;
                        // width: max-content;
                        display: flex;
                        align-items: center;

                        @include media(mobile){
                            // width: max-content;
                            font-size: 1.5rem;
                            text-align: center;
                            // background-color: palevioletred;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }
                    }



                }
            }

            .description{
                @include media(mobile){
                    // display: none;
                }
            }
        }

        
        .i-scroll{
            width: 2rem;
            height: 2rem;
            position: absolute;
            bottom: 1.4rem;
            z-index: 5;
        }
    }

    .i-right{
        flex: 1;
        position: relative;
        @include media(mobile){
            max-width: 100%;
        }
        // overflow: hidden;


        .i-bg{
            clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
            background-color: #59b256;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
        }

        .i-img{
            width: 100%; 
            height: 100%;
            object-fit: cover;
            position: absolute;
        }

    }
}

