@import "../../app.scss";

.toggle{
    width: 3rem;
    height: 1.5rem;
    border-radius: 1.2rem;
    border: 0.1rem solid #999;
    background-color: white;
    position: fixed;
    top: 0.5rem;
    right: 0.5rem;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: space-around;
    transition: all 10s ease;


    .t-icon{
        width: 1rem;
        height: 1rem;
    }

    .t-button{
        width:1.5rem;
        height: 1.5rem;
        border-radius:50%;
        background-color: #999;
        position: absolute;
        cursor: pointer;
        // z-index: 4;
        transition: all 0.3s ease;


        // &:hover{
        //     transform: translateX(-1.5rem);
        // }
    }
}