@import "../../app.scss";

.a{
    height: 100vh;
    display: flex;
    align-items: center;
    // background-color: aquamarine;

    @include media(mobile){
        flex-direction: column;
        margin-top: 2rem;
        text-align: center;
        // margin-bottom: 7rem;
        height: auto;
    }

    .a-left{
        flex: 1;
        // background-color: yellow;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        @include media(mobile){
            width: 100%;
            margin-bottom: 3rem;
        }

        // overflow: hidden;



        .a-card{
            position: relative;
            width: 60%; 
            height: 70vh;
            border-radius: 2rem;
            overflow: hidden;
            // background-color: red;
            @include media(mobile){
                // width: 100%;
                height: 30vh;
            }
            &.bg{
                position: absolute;
                top: 3rem;
                left: 3rem;
                background-color: #333;
                // z-index: 2;
                @include media(mobile){
                    top: 1rem;
                    left: 4rem;
                    // width: 100%;
                    // display: none;
                }
            }

            .a-img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                // display: none;
            }

        }
    }
    .a-right{
        flex: 1;
        // background-color: lime;

        // overflow: hidden;
        @include media(mobile){
            display: flex;
            flex-direction: column;
            padding:0 1.2rem 1.2rem 1.2rem;
        }

        .a-title{
            font-weight: 400;
        }
        .a-sub{
            margin: 1.3rem 0;
        }
        .a-description{
            font-weight: 300;
        }
        .a-award{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 3rem;
            // text-align: right ;
            @include media(mobile){
                // display: flex;
                // flex-direction: column;
            }

            .a-award-img{
                // width: 100%;
                max-width: 9rem;
                max-height: 9rem;
                border-radius: 1.4rem;
            }

            .a-award-texts{
                width: 70%;
                @include media(mobile){
                    // display: flex;
                    // flex-direction: column;
                }

                .a-award-title{
                    color: #555;
                    font-weight: bold;
                    margin-bottom: 1rem;
                }
                .a-award-description{

                }
            }
        }
    }

}